.left {
    width: 62.35rem;
    /* height: 95.3rem; */
    margin-top: 5rem;
    /* background-color: antiquewhite; */
}
.right {
    width: 31.1rem;
    /* height: 95.7rem; */
    display: grid;
    grid-template-rows: 20.5rem 20.5rem;
    /* grid-template-columns: 60.2rem; */
    /* background-color: antiquewhite; */
    margin-top: 4rem;
    margin-left: 1rem;
    gap: 0.6rem;
}
/* 左侧样式 */
.left-top {
    width: 62.35rem;
    /* height: 18rem; */
    display: grid;
    grid-template-rows: 6.65rem;
    grid-template-columns: 14.8rem 14.8rem 14.8rem 14.8rem;
    gap: 1rem;
    /* padding-top: 3.8rem; */
    /* background-color: #fff; */
}
.title{
    padding-top: 0.65rem;
}
.left-bottom {
    width: 62.35rem;
    /* height: 79.74em; */
    margin-top: 0.75rem;
}
.left-bottom-box {
    width: 62.35rem;
    /* height: 37rem; */
    display: grid;
    grid-template-columns: 41.2rem 20.1rem;
    grid-template-rows: 16.67rem;
    gap: 0.75rem;
    margin-bottom: 0.75rem;
}
.left-top-box {
    background: url(../image/left-top.png) no-repeat center center;
    background-size: 100% 100%;
    /* background-color: aquamarine; */
}
.item1{
    background: url(../image/left-bottom-left.png) no-repeat center center;
    background-size: 100% 100%;
    /* background-color: #fff; */
}
.item2 {
    background: url(../image/left-bottom-right.png) no-repeat center center;
    background-size: 100% 100%;
    /* background-color: aquamarine; */
}
.title-data {
    padding-left: 1rem;
    font-size: 0.8rem;
    /* font-family: 'KINGDEEKB-Regular'; */   
    font-family: 'FZLTZHUNHJW--GB1-0, FZLTZHUNHJW--GB1';
    font-weight: 400;
    color: #00D2FF;
    line-height: 1.05rem;  
    letter-spacing: 0.05rem;
}
.num {
    margin: 0.35rem 4.3rem;
    text-align: center;
}
.digit {
    font-size: 3.4rem;
    font-family: 'KINGDEEKB-Regular';
    font-weight: 400;
    color: #36F497;
    line-height: 4.5rem;
    letter-spacing: 0.15rem;

}
.unit {
    font-size: 0.9rem;
    /* font-family: 'FZLTZHUNHJW--GB1-0, FZLTZHUNHJW--GB1'; */
    font-weight: normal;
    color: #FFFFFF;
    line-height: 1.15rem;
    letter-spacing: 0.05rem
}
.title1 {
    padding-top: 0.1rem;
}
.small {
    padding-left: 0.1rem;
    font-size: 0.5rem;
}
.chart {
    height: 17.67rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: azure; */
}
/* 右侧样式 */
.item {
    background: url(../image/right.png) no-repeat;
    background-size: 100% 100%;
}
.table-item {
    width: 31.1rem;
    height: 19rem;
    overflow: hidden;
    /* background-color: aquamarine; */
}
table {
    width: 100%;
    margin-top: 1rem;
    border-collapse:collapse;
    /* background-color: darkorange; */
}
.right-top tr {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1.6fr;
}
.table-item thead > tr {
    background-color: rgba(83, 88, 113, .2);
}
.table-item thead > tr th:nth-child(1) {
    padding-left: 1.45rem;
}
.table-item tbody tr td:nth-child(4) {
    padding-left: 1rem;
}
.table-item tbody tr td:nth-child(5) {
    padding-right: 1.45rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.table-item th {
    height: 2.42rem;
    font-size: 0.7rem;
    /* font-family: 'STHeitiSC-Light, STHeitiSC'; */
    font-weight: 300;
    color: #FFFFFF;
    line-height: 2.42rem;
    text-align: left;
    
}
.table-item td { 
    font-size: 0.9rem;
    font-family: 'STHeitiSC-Medium, STHeitiSC';
    font-weight: 500;
    line-height: 2.5rem;
    height: 2.5rem;
    text-align: left;
}  
.tds {
    position: relative;
}
.tds span {
    position: absolute;
    top: 20%;
    width: 1.55rem;
    height: 1.55rem;
    border-radius: 50%;
    margin-left: 1.6rem;
}
.right-bottom  tr {
    display: grid;
    grid-template-columns: 1fr 3fr 1.2fr;
}
.right-bottom td:nth-child(3) {
    padding-left: 1.75rem;
}
.right-bottom tr td:nth-child(2) {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* 刷新界面按钮 */
.refresh {
    position: absolute;
    top: 1.7rem;
    left: 1rem;
    width: 5.4rem;
    height: 1.6rem;
    background-color:rgba(83, 88, 113, .2) !important;
}

/* 共享中心按钮 */
.share {
    position: absolute;
    top: 1.7rem;
    right: 19rem;
    width: 11.5rem;
    height: 1.6rem;
}
/* antd 共享中心 样式修改 */
.ant-select:not(.ant-select-customize-input) .ant-select-selector {
    background-color:  rgba(83, 88, 113, .2) !important;
    border: 0 !important;
}
.ant-select-selection-placeholder {
    color: #fff !important;
    font-size: 14px !important;
}
.ant-select-arrow {
    color: #fff !important;
}
.ant-select {
    color: #fff !important;
}
.ant-select-dropdown {
    background-color:  rgba(27, 33, 58, 1) !important;;
}
.ant-select-item {
    color: #fff !important;
}
.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
    background-color: rgba(27, 33, 58, 1) !important;
    color: rgba(24, 144, 255, 1) !important;
}
.ant-select-item-option-active:not(.ant-select-item-option-disabled) {
    background-color: rgba(27, 33, 58, 0.5) !important;
    color: rgba(24, 144, 255, 1) !important;
}

/* 日历按钮 */
.date {
    position: absolute;
    right: 7rem;
    top: 1.7rem;
    /* background-color: #FFFFFF; */
}
/* antd 日历 样式修改 */
.ant-picker-range  {
    background-color: rgba(83, 88, 113, .2) !important;
    border: 0 !important;
    width: 11.5rem !important;
    height: 1.6rem ;
}
.ant-picker-input > input, .anticon-swap-right, .anticon-calendar, .ant-picker-header, .ant-picker-content th, .ant-picker-header button, .ant-picker-cell-in-view .ant-picker-cell-inner {
    color: #fff !important;
}
.ant-picker-input > input {
    font-size: 14px !important;
}
.ant-picker-clear {
    background: rgba(83, 88, 113, 0.1) !important;
}
.ant-picker-cell .ant-picker-cell-inner {
    color: rgba(255, 255, 255, 0.25);
    border-radius: 50% !important;
}
.ant-picker-cell .ant-picker-cell-inner:hover {
    background: rgba(24, 141, 240, 1) !important;
}
.ant-picker-panel-container {
    background: transparent !important;
}
.ant-picker-header, .ant-picker-date-panel {
    border: 0 !important;
}
.ant-picker-panel {
    background-color: #151B30 !important;
    border: 0 !important;
}
.ant-picker-cell-in-view.ant-picker-cell-in-range::before, .ant-picker-cell-in-view.ant-picker-cell-range-start:not(.ant-picker-cell-range-start-single)::before, .ant-picker-cell-in-view.ant-picker-cell-range-end:not(.ant-picker-cell-range-end-single)::before {
    opacity: .1 !important;
}

.ant-picker-panel,
.ant-picker-footer {
  background-color: #151b30 !important;
}
.ant-picker-cell-disabled::before {
  background-color: #fff;
  opacity: 0.1;
}
.ant-picker-cell:hover:not(.ant-picker-cell-in-view) .ant-picker-cell-inner,
.ant-picker-cell:hover:not(.ant-picker-cell-selected):not(.ant-picker-cell-range-start):not(.ant-picker-cell-range-end):not(.ant-picker-cell-range-hover-start):not(.ant-picker-cell-range-hover-end)
  .ant-picker-cell-inner {
  background-color:  rgba(24, 141, 240, 1) !important;
}
.ant-picker-ranges .ant-picker-preset > .ant-tag-blue {
  color: #b6b6b6;
  background: rgba(204, 204, 204, 0.1) !important;
  border-radius: 0.1rem;
  border: 0.05rem solid rgba(153, 153, 153, 0.4);
  cursor: pointer;
}

.ant-picker-ranges .ant-picker-preset > .ant-tag-blue {
    color: #fff !important;
    background-color:  rgba(204, 204, 204, 0.1) !important;
    border-color: rgba(204, 204, 204, 0.1) !important;
}
.ant-picker-ranges .ant-picker-preset > .ant-tag-blue:hover {
    border-color:  rgba(24, 141, 240, 1) !important;
    color: rgba(24, 141, 240, 1) !important;
}

/* button 查询按钮 */
.search {
    position: absolute;
    right: 2.5rem;
    top: 1.7rem;
}
.ant-btn {
    font-size: 14px !important;
    color: #fff !important;
    background-color: rgba(204, 204, 204, 0.1) !important;
    border: 0 !important;
}
.ant-btn:hover {
    color: rgba(24, 141, 240, 1) !important;
}