.table-play {
    width: 100%;
    margin-top: 0;
    /* background-color: rgba(83, 88, 113, 0.8); */
}
.table-play tr {
    height: 2.75rem;
    color: #fff;
    /* font-size: 2rem; */
}
.rank img, .rank p {
    width: 1.6rem;
    height: 1.55rem;
    line-height: 1.55rem;
    font-style: normal;
    font-size: 0.72rem;
    text-align: center;
    color: #FFFFFF;
}
.rank p {
    position: relative;
    left: 30%;
    top: 20%;
    background-color: #35425E;
    margin: 0;
    border-radius: 50%;
}
.rank p > span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
}