@font-face{
    font-family: 'KINGDEEKB-Bold'; 
    src: url('../font/KINGDEEKB-Bold.otf');
}
@font-face{
    font-family: 'KINGDEEKB-Light'; 
    src: url('../font/KINGDEEKB-Light.otf');
}
@font-face{
    font-family: 'KINGDEEKB-Regular'; 
    src: url('../font/KINGDEEKB-Regular.otf');
}
.container {
    box-sizing: border-box;
    /* width: 192rem;
    height: 108rem; */
    width: 100%;
    height: 100vh;
    background: url(../image/bg.png) no-repeat;
    background-size: 100%;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem;
}
.orange {
    color: #FFA940;
}
.unset {
    color: unset;
}
.colorOne{
    color:rgba(255, 148, 0, 1) ;
}
.colorTwo {
    color:rgba(150, 191, 255, 1);
}
.colorThree {
    color: rgba(255, 189, 146, 1);
}
.one {
    background: url(../image/one.png) no-repeat center center;
    background-size: 100%;
}
.two {
    background: url(../image/two.png) no-repeat center center;
    background-size: 100%;
}
.three {
    background: url(../image/three.png) no-repeat center center;
    background-size: 100%;
}
.color4 {
    color: rgba(255, 255, 255, 1);
}
ul,li {
    list-style:none
}
a {
    text-decoration: none;
}